<!-- *************************************************************************
     TEMPLATE
     ************************************************************************* -->

<template lang="pug">
div(
  :class=`[
    "c-snapchat-profile-box",
    "c-snapchat-profile-box--" + size,
    "c-snapchat-profile-box--" + theme
  ]`
)
  div(
    v-if="titleContent"
    v-html="titleContent"
    class="c-snapchat-profile-box__header"
  )

  .c-snapchat-profile-box__information
    img(
      @click="onGoToSnapchat"
      :src="snapchatAvatar"
      class="c-snapchat-profile-box__avatar"
    )

    span.c-snapchat-profile-box__name {{ snapchatName }}

    snapchat-button(
      @click="onGoToSnapchat"
      :size="size"
    ) {{ buttonContent }}
</template>

<!-- *************************************************************************
     SCRIPT
     ************************************************************************* -->

<script>
// PROJECT: COMPONENTS
import SnapchatButton from "@/components/snapchat/SnapchatButton"

// PROJECT: MIXINS
import EditorMixin from "@/mixins/common/EditorMixin"
import SizeMixin from "@/mixins/common/SizeMixin"
import SnapchatProfileBoxMixin from "@/mixins/snapchat/SnapchatProfileBoxMixin"
import ThemeMixin from "@/mixins/common/ThemeMixin"
import WidgetMixin from "@/mixins/common/WidgetMixin"

export default {
  name: "SnapchatProfileBox",

  components: {
    SnapchatButton
  },

  mixins: [
    EditorMixin,
    SizeMixin,
    SnapchatProfileBoxMixin,
    ThemeMixin,
    WidgetMixin
  ],

  methods: {
    // --> EVENT LISTENERS <--

    onGoToSnapchat() {
      window.open(`https://www.snapchat.com/add/${this.snapchatName}`, "_blank")
    }
  }
}
</script>

<!-- *************************************************************************
     STYLE
     ************************************************************************* -->

<style lang="scss">
// VARIABLES
$c: ".c-snapchat-profile-box";
$sizes: "mini", "small", "default", "medium", "large";

#{$c} {
  display: flex;
  flex-direction: column;

  #{$c}__header {
    border-bottom-width: 1px;
    border-bottom-style: solid;
    text-align: center;
    font-weight: 600;
  }

  #{$c}__information {
    display: flex;
    align-items: center;
    flex-direction: column;

    #{$c}__avatar {
      cursor: pointer;
    }

    #{$c}__name {
      font-weight: 800;
    }
  }

  // --> SIZES <--

  @each $size in $sizes {
    $i: index($sizes, $size) - 1;

    &--#{$size} {
      #{$c}__header {
        padding: (8px + (1px * $i)) (20px + (2px * $i));
        font-size: 12px + (1px * $i);
        line-height: 16px + (1px * $i);
      }

      #{$c}__information {
        padding: 14px + (2px * $i);

        #{$c}__avatar {
          width: 60px + (10px * $i);
          height: 60px + (10px * $i);
          margin-bottom: 10px + (1px * $i);
        }

        #{$c}__name {
          font-size: 13px + (1px * $i);
          line-height: 13px + (1px * $i);
          margin-bottom: 11px + (1px * $i);
        }
      }
    }
  }

  // --> THEMES <--

  @each $theme in $themes {
    $themeName: map-get($theme, "name");

    &--#{$themeName} {
      #{$c}__header {
        border-bottom-color: mdg($theme, "borders", "default", "primary");
        background-color: mdg($theme, "backgrounds", "default", "secondary");
        box-shadow: 0 1px 3px mdg($theme, "shadows", "default", "primary");
      }
    }
  }
}
</style>
